"use client";

import React, { useState } from "react";
import { Modal } from "./ui/Modal";
import { Input } from "./ui/Input";
import { Button } from "./ui/Button";

interface EditGroupModalProps {
  isOpen: boolean;
  onClose: () => void;
  onUpdate: (name: string) => void;
  currentName: string;
}

export const EditGroupModal: React.FC<EditGroupModalProps> = ({
  isOpen,
  onClose,
  onUpdate,
  currentName
}) => {
  const [groupName, setGroupName] = useState(currentName);

  React.useEffect(() => {
    setGroupName(currentName);
  }, [currentName, isOpen]);

  const handleSubmit = () => {
    if (groupName.trim() && groupName.trim() !== currentName) {
      onUpdate(groupName.trim());
      onClose();
    }
  };

  const handleClose = () => {
    setGroupName(currentName);
    onClose();
  };

  return (
    <Modal
      isOpen={isOpen}
      onClose={handleClose}
      title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –Ω–∞–∑–≤–∞–Ω–∏–µ –≥—Ä—É–ø–ø—ã"
      data-oid="sfnmy54">

      <div className="space-y-4" data-oid="i7mw13c">
        <Input
          label="–ù–∞–∑–≤–∞–Ω–∏–µ –≥—Ä—É–ø–ø—ã"
          value={groupName}
          onChange={setGroupName}
          placeholder="–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –≥—Ä—É–ø–ø—ã"
          required
          data-oid="d9hth-q" />


        <div className="flex space-x-3" data-oid="fa6n1uz">
          <Button
            onClick={handleSubmit}
            disabled={!groupName.trim() || groupName.trim() === currentName}
            data-oid=".tsocs_">

            –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
          </Button>
          <Button onClick={handleClose} variant="secondary" data-oid="ka4t9su">
            –û—Ç–º–µ–Ω–∞
          </Button>
        </div>
      </div>
    </Modal>);

};